@use 'variables' as v;

@mixin telefono{
    @media (min-width: v.$telefono){ 
        @content;
    }
}
@mixin tablet{
    @media (min-width: v.$tablet){ 
        @content;
    }
}
@mixin desktop{
    @media (min-width: v.$desktop){ 
        @content;
    }
}
@mixin desktopXL{
    @media (min-width: v.$desktopXL){ 
        @content;
    }
}
@mixin contenedor{
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}
@mixin grid($columnas: 2, $separacion: 2rem){
    display: grid;
    grid-template-columns:repeat($columnas, 1fr);
    gap: $separacion;
}
@mixin reset_lista{
    margin: 0;
    padding: 0;
    list-style: none;
}