@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.contenedor_boletos{
    background-color: v.$verde;
    h3{
        color: v.$blanco;
    }
    .boletos{
        @include m.contenedor;
        @include m.tablet{
            @include m.grid(2, 5rem);
        }
    }
}

.pase{
    transition: transform .3s ease-in-out;
    &:hover{
        transform: scale(1.1);
    }
    &_nombre{
        font-size: 3.6rem;
        font-weight: bold;
        text-align: center;
        color: v.$blanco;
    }
    &_header,
    &_footer{
        padding: .5rem;

        p{
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }
    &_header{
        background-color: v.$rosa;
        p{
            text-transform: uppercase;
            font-size: 2.2rem;
            padding: 1rem 0;
        }
    }
    &_body{
        background-color: v.$blanco;
        ul{
            @include m.reset_lista;
            li{
                padding: 2rem;
                border-bottom: 1px solid #E1E1E1;
            }
        }
    }
    &_footer{
        background-color: v.$blanco;
        padding: 3rem;
        p{
            font-size: 2.8rem;
            font-weight: bold;
            background-color: v.$morado;
            padding: 1rem 0;
            border-radius: 1rem;

            &:hover{
                background-color: color.scale(v.$morado, $lightness: -10%);
            }
        }
    }
}